body {
  margin: 0;
  padding: 0;
}

.App {
  font-family: 'Libre Baskerville', sans-serif;
}

.TambolaGameManager {
    text-align: center;
    width: 96%;
    margin: auto;
    display: grid;
    grid-template-columns: auto;
    align-items: center;
    font-family: inherit;
}

.Header {
    margin: 5px 5px 0;
    padding: 0;
}

.GameControls {
    display: flex;
    flex-direction: row;
    justify-content: center;
    font-family: inherit;
}

.CoinPublisher {
    position: absolute;
    top: 200px;
    left: 55px;
    font-family: inherit;
}

.Footer {
    margin: 4px;
    color: darkslategray;
}

.Footer a {
    text-decoration: none;
    color: navy;
}

@media (min-width: 500px) {
    .CoinPublisher {
        top: 80px;
        left: 200px;
    }
}

@media (min-width: 800px) {
    .CoinPublisher {
        top: 80px;
        left: 430px;
    }
}

.TambolaBoard {
    display: grid;
    grid-template-columns: 20% 20% 20% 20% 20%;
    grid-column-gap: 0px;
    grid-row-gap: 0px;
    justify-items: center;
}

@media (min-width: 500px) {
    .TambolaBoard {
        grid-template-columns: 10% 10% 10% 10% 10% 10% 10% 10% 10% 10%;
    }
}

@media (min-width: 900px) {
    .TambolaBoard {
        grid-template-columns: 6.67% 6.67% 6.67% 6.67% 6.67% 6.67% 6.67% 6.67% 6.67% 6.67% 6.67% 6.67% 6.67% 6.67% 6.67%;
    }
}
.TambolaBoardBox {
    box-sizing: border-box;
    border: 1px solid black;
    display: flex;
    justify-content: center;
    align-items: center;
    width: 100%;
    height: 60px;
    background-color: lightyellow;
    font-weight: bold;
    font-size: 28px;
    cursor: cell;
    color: rgb(131, 128, 128);
}

@media (min-width: 500px) {
    .TambolaBoardBox {
        border: 2px solid black;
        width: 100%;
        height: 90px;
        font-size: 32px;
    }
}
.TambolaBoardCoin {
    display: flex;
    justify-content: center;
    align-items: center;
    font-weight: bold;
    font-size: 28px;
    color: black;
    box-sizing: border-box;
    box-shadow: -5px 3px rgb(156, 85, 78);
    text-shadow: -2px 1px rgb(56, 56, 56);
    border: 2px solid salmon;
    background-color: salmon;
    border-radius: 50%;
    width: 70%;
    height: 80%;
    padding: 1%;
    z-index: 5;
}

@media (min-width: 500px) {
    .TambolaBoardCoin {
        font-size: 32px;
        box-shadow: -6px 4px rgb(156, 85, 78);
        width: 80%;
    }
}
.Button {
    background-color: burlywood;
    box-shadow: -3px 2px gray;
    border: 2px solid gray;
    border-radius: 5px;
    color: white;
    outline: none;
    cursor: pointer;
    font: inherit;
    padding: 10px;
    margin: 10px;
    font-weight: bold;
    font-size: 20px;
    z-index: 5;
    width: 150px;
}

.Button:disabled {
    color: darkgrey;
    cursor: not-allowed;
}

.Success {
    color: rgb(58, 128, 52);
}

.Danger {
    color: rgb(252, 48, 48);
}

@media (min-width: 500px) {
    .Button {
        width: 200px;
    }
}
.CoinPublisher {
    -webkit-animation: FadeAnimation 1.5s ease-in .2s forwards;
            animation: FadeAnimation 1.5s ease-in .2s forwards;
    box-sizing: border-box;
    border: 2px solid salmon;
    background-color: salmon;
    color: black;
    border-radius: 50%;
    display: flex;
    justify-content: center;
    align-items: center;
    position: fixed;
    font-weight: bold;
    z-index: 100;
    width: 80%;
    height: 45%;
    font-size: 170px;
    box-shadow: -25px 20px rgb(156, 85, 78);
    text-shadow: -4px 3px rgb(56, 56, 56);
    cursor: cell;
}

@-webkit-keyframes FadeAnimation {
    0% {
      opacity: 1;
      visibility: visible;
    }
  
    100% {
      opacity: 0;
      visibility: hidden;
    }
}

@keyframes FadeAnimation {
    0% {
      opacity: 1;
      visibility: visible;
    }
  
    100% {
      opacity: 0;
      visibility: hidden;
    }
}

@media (min-width: 500px) {
    .CoinPublisher {
        width: 55%;
        height: 60%;
        font-size: 250px;
        box-shadow: -25px 20px rgb(156, 85, 78);
    }
}

@media (min-width: 800px) {
    .CoinPublisher {
        width: 40%;
        height: 80%;
        font-size: 350px;
        box-shadow: -35px 30px rgb(156, 85, 78);
    }
}

